import { useDispatch, useSelector } from 'react-redux';
import { startIdProductOnInvoiceActiveAux } from '../../actions/invoice';

import { uiOpenModalArticles, uiOpenModalEditArticles } from '../../actions/ui';
import addIcon from '../../assets/addIcon.svg';

import './facturas.css';

const InvoiceArticlesListCard = (props) => {

    const dispatch = useDispatch();

    const { blockCharge } = props;

    const { activeInvoice } = useSelector( state => state.invoice );

    const handleAddArticle = () => {
        dispatch( uiOpenModalArticles() );
    }

    const handleActionArticle = (idProduct) => {
        //Abrir modal de agregar, editar o eliminar un artículo.
        dispatch( uiOpenModalEditArticles() );
        //Enviar el id del artículo a modificar
        dispatch( startIdProductOnInvoiceActiveAux( idProduct ) );
    }

    const filterProductsByValuesExist = ( listProducts ) => {
        const productToList = listProducts.filter(product => 
            ( product.total && product.total.trim().length > 0 )
        );

        return productToList;
    }

    return (
        <div className='articles-invoice-container'>
            <div className='action-button float-button'>
                <button
                    disabled={ blockCharge 
                                ? true 
                                : ( activeInvoice[0] && activeInvoice[0].cliente 
                                    && activeInvoice[0].cliente.length > 0 ) 
                                    ? false
                                    : true 
                            }
                    className={blockCharge 
                                ? 'btn-add-invoice disabled-button-action' 
                                : ( activeInvoice[0] && activeInvoice[0].cliente 
                                    && activeInvoice[0].cliente.length > 0 ) 
                                    ? 'btn-add-invoice'
                                    : 'btn-add-invoice disabled-button-action' 
                            }
                    onClick={blockCharge 
                                ? undefined
                                : ( activeInvoice[0] && activeInvoice[0].cliente 
                                    && activeInvoice[0].cliente.length > 0 ) 
                                    ? handleAddArticle
                                    : undefined  
                            }
                >
                    <img src={addIcon} alt='new' />
                </button>
            </div>

            <div className='articles-list-invoice'>
                <div className='list-invoice'>
                    {( activeInvoice[0] && activeInvoice[0].listProducts 
                        && activeInvoice[0].listProducts.length > 0 )

                            && (filterProductsByValuesExist(activeInvoice[0].listProducts).length > 0)
                                ?  filterProductsByValuesExist(activeInvoice[0].listProducts).map( 
                                    (product, index) => (
                                        <div
                                            onClick={ blockCharge 
                                                        ? () => {} 
                                                        : () => handleActionArticle(product.codigo.trim()) 
                                                    }
                                            key={ index }
                                        >
                                            <div className='articles-invoice'>
                                                <div className='article-info-invoice'>
                                                    <div className='article-info-invoice-items'>
                                                        <p className='article-info-invoice-title'>Bodega:</p>
                                                        <p className='article-info-invoice-detail'>
                                                            { product.bodega.trim() }
                                                        </p>
                                                    </div>
                                                    
                                                    <div className='article-info-invoice-items'>
                                                        <p className='article-info-invoice-title'>Código:</p>
                                                        <p className='article-info-invoice-detail'>
                                                            { product.codigo.trim() }
                                                        </p>
                                                    </div>

                                                    <div className='article-info-invoice-items'>
                                                        <p className='article-info-invoice-title'>Nombre:</p>
                                                        <p className='article-info-invoice-detail 
                                                            name-article-invoice'>
                                                            { product.detalle.trim() }
                                                        </p>
                                                    </div>
                                                </div>

                                                <div className='article-values-invoice'>
                                                    <div className='article-info-invoice-items space-between'>
                                                        <p className='article-info-invoice-title'>Cantidad:</p>
                                                        <p className='article-info-invoice-detail'>
                                                            { parseFloat(product.cantidad).toFixed(2) }
                                                        </p>
                                                    </div>

                                                    <div className='article-info-invoice-items space-between'>
                                                        <p className='article-info-invoice-title'>Precio:</p>
                                                        <p className='article-info-invoice-detail'>
                                                            { parseFloat(product.precio).toFixed(2) }
                                                        </p>
                                                    </div>

                                                    <div className='article-info-invoice-items space-between'>
                                                        <p className='article-info-invoice-title'>%Descto:</p>
                                                        <p className='article-info-invoice-detail red'>
                                                            { '-'+parseFloat(product.descto).toFixed(2)+' %' }
                                                        </p>
                                                    </div>
                                                    <hr className='hr-articles-invoice'/>

                                                    <div className='article-info-invoice-items space-between'>
                                                        <p className='article-info-invoice-title'>TOTAL:</p>
                                                        <p className='article-info-invoice-detail green'>
                                                            { '$'+parseFloat(product.total).toFixed(2) }
                                                        </p>
                                                    </div>
                                                </div>
                                            </div>
                                            <hr className='hr-articles-invoice item'/>
                                        </div>
                                    ))

                            :   <div className='article-invoice'></div>
                    }
                </div>
            </div>
        </div>
    )
}

export default InvoiceArticlesListCard;